.kanban-board {
   
    gap: 20px;
    flex-grow: 1; /* Allow the board to expand and take remaining space */
  overflow-y: auto;
  overflow-x: auto;
    
  }
  .kanban-columns {
    display: flex; /* Use flex to arrange columns side by side */
    flex-wrap: nowrap; /* Prevent columns from wrapping */
    justify-content: flex-start;
    
  }
  .kanban-column {
    border-radius: 0.5px;
  padding: 5px ;
  width: 400px; 
  display: flex;
  flex-direction: column;
  align-items: stretch;     /* Make sure the items stretch to the width of the column */
  box-sizing: border-box;
  
  
  }
  
  .kanban-column p {
    text-align: left;
    margin-left: 20px;

  }
  
  .kanban-columns {
    display: flex; /* Display columns in a row */
    gap: 20px; /* Space between columns */
  }

  .ticket-row {
    display: flex; /* Flexbox for tickets */
    flex-wrap: wrap; /* Allow wrapping of items */
  justify-content: flex-start; /* Space between ticket cards */
  }
  
  .ticket-card {
    background-color: #fef8f8;
    border-radius: 2px;
    
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  }

  .kanban-header {
     /* Space between header and columns */
    padding: 10px; /* Optional padding */
    background-color: white; /* Light background for visibility */
     /* Rounded corners */
    text-align: left;
   
}

.count{
    color: grey;
}

.kanban-header-icon {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 15px;
  }
  
  .group-details {
    display: flex;
    align-items: center;
    gap: 0px; /* Space between icon and text */
  }
  
  .header-icons {
    display: flex;
    gap: 10px; /* Space between icons */
  }
  
  .header-icon {
    width: 20px; /* Adjust the size as needed */
    height: 20px;
    cursor: pointer;
  }
  
  .priority-icon {
    width: 20px;
    height: 20px;
  }
  