.card {
    background-color: white;
    border-radius: 8px;
    padding: 5px;
    margin-bottom: 4px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    text-align: left; /* Align text to the left */
    width: 300px; /* Set fixed width */
    height: 130px; /* Set fixed height */
    display: flex; /* Use flexbox */
    flex-direction: column; /* Arrange items in a column */
    justify-content: space-between; /* Space between items */
  }
  
  
  .small-button:hover {
    background-color: white; /* Darker grey on hover */
  }
  

  .ticket-row {
    display: flex; /* Flexbox for tickets */
    flex-wrap: wrap; /* Allow wrapping of items */
  justify-content: flex-start; /* Space between ticket cards */
  }
  
  .feature-request {
    display: flex;
    align-items: center;
    gap: 8px; /* Space between the icon and text */
    cursor: pointer;
  }
  
  .todo-icon {
    width: 16px; /* Adjust icon size */
    height: 16px;
  }
  
  .small-button {
    font-size: 14px; /* Adjust text size if needed */
    
    cursor: pointer;
  }
  
  .action-row {
    display: flex;
    align-items: center;
    gap: 10px; /* Space between button and icon box */
    margin-top: 8px;
  }
  
  .small-button {
    background-color: white;
    border: 1px solid #ccc;
    padding: 6px 10px;
    cursor: pointer;
    font-size: 14px;
    color: grey;
    white-space: nowrap; /* Ensure the text doesn't wrap */
    border-radius: 4px;
  }
  
  .icon-box {
    background-color: white;
    border: 1px solid white;
    padding: 5px;
    border-radius: 4px;
  }
  
  .menu-icon {
    width: 16px; /* Adjust the size of the icon */
    height: 16px;
  }
  
  .card-header {
    display: flex;
    align-items: center; /* Aligns items vertically in the center */
  }
  

  .feature-request{
    margin-right: 8px; /* Space between the icon and the title */
    width: 15px; /* Adjust width */
    height: 15px; /* Adjust height */
    padding: 2px;
  }

  .very-small-button {
    background-color: white;
    color: rgba(78, 78, 78, 0.018);
    border: 1px solid rgba(0, 0, 0, 0.2); /* Lighter black border */
    border-radius: 4px; /* Optional: Add rounded corners */
    padding: 2px; /* Adjust padding if necessary */
  }
  
  .menu-icon {
    width: 15px;
    height: 15px;
    padding: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
  }
  