/* DisplaySettings.css */

/* Full-width white background for the header */
.display-settings-header {
  border-radius: 0;
  text-align: left;
  position: relative;
 
    background-color: white;
    
  }
  
  /* Container for display settings */
  .display-settings-container {
    position: relative;
    /* padding: 8px 6px; */
    display: flex;
    justify-content: left; /* Center the button */
  }
  
  /* Square display button */
  .display-button {
    background-color: white;
    color: black;
    
    font-size: 12px;
    border: none;
    border-radius: 1px;
    cursor: pointer;
    width: 100px;   /* Make the button square */
    height: 30px;  /* Same width and height */
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
     /* Optional: Border to highlight square shape */
  }
  
  /* Dropdown box with normal size */
  .dropdown-box {
    position: absolute;
    top: 45px;
    left: 0;
    background-color: white;
    box-shadow: 0px 2px 8px  rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    padding: 20px;
    width: 200px;
    z-index: 10;
    align-items: center;
  }
  
  /* Section inside dropdown */
  .dropdown-section {
    margin-bottom: 1px;
  }
  
  /* Labels */
  .dropdown-section label {
    font-size: 14px;
    color: white;
    margin-right: 10px;
  }
  
  /* Dropdowns */
  .dropdown-section select {
   
  font-size: 14px;
  border-radius: 4px;
  border: 1px solid white;
  width: 100%;
  }
  
  .display-settings {
    display: flex;
    align-items: center;
   
  }
  
  .display-icon {
    margin-left: 4px; /* Adjust as needed for spacing */
    width: 20px; /* Set width */
    height: 20px; /* Set height */
    
  }
  
  .small-button {
    background-color: white;
   
    
    cursor: pointer;
    font-size: 14px;
    color: grey;
    white-space: nowrap; /* Ensure the text doesn't wrap */
    border-radius: 4px;
    display: flex; /* Ensure contents are aligned in a row */
  align-items: center;
  }
  
  .display-settings-container {
    display: flex;
    align-items: center; /* Align items vertically */
    
  }
  
