.modal-overlay {
    position:absolute;
    
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: left;
    align-items: center;
    
  }
  
  .modal-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
    max-width: 500px;
    width: 90%;
  }
  
  .close-button {
    background: transparent;
    border: none;
    font-size: 18px;
    cursor: pointer;
    background-color: white;
  }
  